.user-admin {
   position: relative;
   top: 0;
   @include right(0);
   z-index: 1000;

   @include media($small) {
      position: absolute;
      @include lrswap(margin, 18px 10px 0 0);
   }

   &.logged-out {

      @include media($small) {
         @include lrswap(margin, 27px 10px 0 0);
      }

      a {
         @extend .delta;
         color: $link-color;
         @include margin-right($sm-spacing);

         @include media($small) {
            color: $link-color-lt; //make color lighter when it is on dark bg
         }

         &:hover {
            border-bottom: $base-border;
         }

      }

   }

   .user-avatar {
      @include float(left);

      img {
         position: relative;
         height: 30px;
         min-width: 30px;
         @include equal-radius(100px);
      }
   }

   .username {
      position: relative;
      top: -10px;
      @include float(left);
      color: $white;
      font-weight: bold;
      @include lrswap(padding, 10px 10px 0 0);
      @include lrswap(margin, $sm-spacing 0 0 $sm-spacing);
   }

   [data-toggle].init {
      @include clearfix;

      &:hover {
         cursor: pointer;
      }

      .username {
         padding-right: 35px;
         @include icon($after, chevron-down) {
            pointer-events: none;
            position: absolute;
            top: 0;
            @include right(0);
            color: $yellow;
            line-height: 36px;
            text-shadow: none;
            padding: 0 8px;
         }
      }

      &.active {

         .username {

            &:before {
               content: '';
               display: inline-block;
               position: absolute;
               top: 0;
               right: 0;
               width: 30px;
               height: 36px;
               @include equal-radius;
               @include border-bottom-radius(0);
               border-top: 0px;
               background: $white;
            }


            &:after {
               @include transform(rotate(180deg));
            }
         }
      }
   }

   .dropdown-menu {
      display: none;
      right: 0 !important;
      left: auto !important;
      @extend .list-unstyled;
      text-align: left;
      border: $base-border;
      border-top: none;
      @include equal-radius;
      padding: $sm-spacing;

      @include media($medium-down) {
         position: relative;
         opacity: 1;
         border: none;
      }

      li {

         @include media($medium-down) {
            padding: 0 $sm-spacing;

            &:hover {
               background-color: transparent;
            }
         }
      }
   }

}
